*,*::before,::after{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

:root{
  --card-col: 1;
  --separator: 1.5rem;
  --bg-color: #131417;
  --cl-white: #fff;
}

html{
  background-color: var(--bg-color);
  color: var(--cl-white);
}


body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 40px;
  font-size: 1.2em;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}

input{
  width: 100%;
  font-size: 1.2rem;
  padding: .5em .8em;
  color: grey;
}

.giftExpertApp{
  margin-bottom: 2rem;
}

.giftExpertApp__title{
  text-align: center;
  margin-bottom: 2.5rem;
  padding: 1.5rem 0;
}

.giftExpertApp__form{
  margin-bottom: 4rem;
}

.giftExpertApp__list{
  list-style: none;
  padding: 0;
}

.giftExpertApp__item{
  margin-bottom: 3rem;
}

.card{
  align-items: stretch;
  margin-bottom: 10px;
  overflow: hidden;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card__item{
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 1rem;
  width: 100%;
  background-color: var(--bg-color);
  color: var(--cl-white);
}

.card__header{
  line-height: 0;
  text-align: center;
}

.card__img{
  height: 178px;
    max-width: 100%;
    object-fit: cover;
    object-position: center;
}

.card__title{
  text-align: center;
  padding: 1rem;
}

.card--m-col-2 .card__item,
.card--m-col-3 .card__item,
.card--m-col-4 .card__item,
.card--md-col-2 .card__item,
.card--md-col-3 .card__item,
.card--md-col-4 .card__item,
.card--d-col-2 .card__item,
.card--d-col-3 .card__item,
.card--d-col-4 .card__item,
.card--dl-col-2 .card__item,
.card--dl-col-3 .card__item,
.card--dl-col-4 .card__item,
.card--dl-col-5 .card__item,
.card--dl-col-6 .card__item{
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: calc(100% / var(--card-col) - (var(--separator) * (var(--card-col) - 1)) / (var(--card-col)));
  margin-right: 0;
  margin-bottom: var(--separator);
}

@media screen and (min-width: 375px){

  .card--m-col-2{
    --card-col: 2;
  }
  .card--m-col-3{
    --card-col: 3;
  }
  .card--m-col-4{
    --card-col: 4;
  }

}


@media screen and (min-width: 768px){

  .card--md-col-2{
    --card-col: 2;
  }
  .card--md-col-3{
    --card-col: 3;
  }
  .card--md-col-4{
    --card-col: 4;
  }
  
}


@media screen and (min-width: 1024px){

  .card--d-col-2{
    --card-col: 2;
  }
  .card--d-col-3{
    --card-col: 3;
  }
  
  .card--d-col-4{
    --card-col: 4;
  }

}

.fade-in{
  animation: fadein 1s 0s 1 ease;
}

@keyframes fadein{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@media screen and (min-width: 1440px){

  .card--dl-col-2{
    --card-col: 2;
  }

  .card--dl-col-3{
    --card-col: 3;
  }
  
  .card--dl-col-4{
    --card-col: 4;
  }

  .card--dl-col-5{
    --card-col: 5;
  }
  
  .card--dl-col-6{
    --card-col: 6;
  }

}